.reservation__container {
  padding: 0 71px;
  display: flex;
  padding-top: 47px;
  justify-content: space-between;

  @media (max-width: $desktop-width) {
    flex-direction: column;
    align-items: center;
    padding-top: 0;
    padding-left: 89px;
  }

  @media (max-width: $tablet-width) {
    padding: 0 15px;
  }
}

.reservation {
  min-height: 555px;
  background: $bg-blue url("../img/reservation-l.webp");
  background-repeat: no-repeat;
  background-position: center top;
  background-size: cover;

  @media (max-width: $tablet-width) {
    min-height: 805px;
    background: $bg-blue url("../img/reservation-m.webp");
    background-repeat: no-repeat;
    background-position: center top;
    background-size: cover;
  }

  @media (min-width: $mobile-width) and (max-width: 600px) {
    min-height: 760px;
    background: $bg-blue url("../img/reservation-s.webp");
    background-repeat: no-repeat;
    background-position: center top;
    background-size: cover;
  }
}

.reservation__text h2 {
  margin: 0;
  font-weight: 700;
  font-size: 40px;
  line-height: 60px;
  padding-top: 165px;
  margin-bottom: 7px;

  @media (max-width: $desktop-width) {
    padding-top: 89px;
    font-size: 34px;
    line-height: 51px;
    margin-bottom: 4px;
    text-align: center;
  }

  @media (max-width: $tablet-width) {
    font-weight: 700;
    font-size: 24px;
    line-height: 31px;
    padding-top: 60px;
    margin-bottom: 13px;
  }
}

.reservation__text p {
  margin: 0;
  font-size: 18px;
  line-height: 27px;
  max-width: 352px;

  & br {
    display: none;
  }

  @media (max-width: $desktop-width) {
    margin-bottom: 51px;
    text-align: center;
    max-width: 416px;
    padding: 0 15px;
  }

  @media (max-width: $tablet-width) {
    font-size: 16px;
    line-height: 22px;
    margin-bottom: 34px;

    & br {
      display: block;
    }
  }
}

.reservation__form form {
  background-color: $main-blue;
  padding: 0 50px;
  max-width: 403px;
  margin-bottom: 46px;

  @media (max-width: $desktop-width) {
    margin-bottom: 98px;
  }

  @media (max-width: $tablet-width) {
    min-width: 290px;
    width: 100%;
    margin-bottom: 65px;
    padding: 0 12%;
  }
}

.reservation__form h3 {
  margin: 0;
  font-weight: 500;
  font-size: 18px;
  line-height: 27px;
  color: $light-grey;
  padding-top: 46px;
  margin-bottom: 26px;

  @media (max-width: $tablet-width) {
    line-height: 27px;
    text-align: center;
    padding-top: 33px;
  }
}

.reservation__form-input input {
  font-size: 14px;
  line-height: 21px;
  color: rgba(1, 28, 64, 0.7);
  text-align: start;
  background-color: $light-grey;
  border: none;
  box-sizing: border-box;
  padding: 11px 16px 11px 16px;
  max-width: 303px;
  width: 100%;
  margin-bottom: 20px;
  border-radius: 0;
  appearance: none;

  @media (max-width: $tablet-width) {
    min-width: 230px;
    margin-bottom: 18px;
  }
}

.reservation__form-input input:active,
.reservation__form-input input:focus {
  color: $main-blue;
  background-color: $light-grey;
  border: none;
  outline: 0;
}

.reservation__personal {
  display: flex;
  align-items: center;
  margin-bottom: 30px;

  & p {
    margin: 0;
    color: $light-grey;
    font-size: 10px;
    line-height: 13px;
    margin-left: 6px;
    cursor: pointer;

    @media (max-width: $tablet-width) {
      max-width: 156px;
    }
  }

  & input {
    cursor: pointer;
    width: 18px;
    height: 18px;
    margin: 0;
    border-radius: 0;

    @media (max-width: $tablet-width) {
      width: 21px;
      height: 21px;
    }
  }
}

.reservation__button-submit input {
  color: $main-blue;
  font-weight: 600;
  font-size: 14px;
  line-height: 21px;
  padding: 10px 90px 12px 89px;
  margin-bottom: 50px;
  cursor: pointer;
  border-radius: 0;
  appearance: none;

  @media (max-width: $desktop-width) {
    padding: 11px 95px 10px 89px;
  }

  @media (max-width: $tablet-width) {
    padding: 9px 57px 11px 53px;
    margin-bottom: 40px;
    min-width: 230px;
    width: 100%;
  }
}
